/* Contact.css */
.contact-section {
  padding: 40px;
  background-color: #f5f5f5;
  text-align: center;
}

h2 {
  font-size: 36px;
  margin-bottom: 20px;
  color: #333;
}

.contact-form-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.contact-form {
  background-color: #fff;
  border-radius: 10px;
  padding: 30px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 600px;
}

.form-group {
  margin-bottom: 20px;
  text-align: left;
}

.form-group label {
  font-size: 16px;
  color: #333;
  margin-bottom: 5px;
  display: block;
}

.form-group input, .form-group textarea {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border-radius: 5px;
  border: 1px solid #ddd;
  background-color: #f9f9f9;
}

.form-group input:focus, .form-group textarea:focus {
  outline: none;
  border-color: #007bff;
}

.submit-btn {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 18px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.submit-btn:hover {
  background-color: #0056b3;
}

.thank-you-message {
  font-size: 1.5em;
  color: green;
}

/* Footer styling */
footer {
  margin-top: 40px;
  padding: 20px;
  background-color: #333;
  color: white;
  text-align: center;
  border-radius: 10px;
}

footer p {
  margin: 10px 0;
  font-size: 14px;
}

.footer-links {
  list-style-type: none;
  padding: 0;
  margin: 10px 0;
}

.footer-links li {
  display: inline;
  margin: 0 10px;
}

.footer-links a {
  color: #ffdd57;
  text-decoration: none;
}

.footer-links a:hover {
  text-decoration: underline;
}
